export default {
  name: "TransformMedia",
  props: {
    file: {
      type: Object,
      required: true
    },
    src: {
      type: String,
      required: true
    },
    isImage: {
      type: Boolean,
      default: true
    },
    isVideo: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      pointers: new Map(),
      // transform state
      scale: 1,
      rotation: 0,
      // 固定为 0, 90, 180, 270
      tx: 0,
      ty: 0,
      // gesture start snapshots
      startScale: 1,
      startRotation: 0,
      startTx: 0,
      startTy: 0,
      startCenter: null,
      startDist: 0,
      startAngle: 0,
      // drag
      dragging: false,
      dragStart: null,
      viewportRect: null,
      // bounds
      minScale: 1,
      maxScale: 4,
      // 手势模式锁定：'pinch' | 'rotate' | null
      gestureMode: null,
      // 旋转是否已触发（防止一次手势多次触发）
      rotateTriggered: false
    };
  },
  computed: {
    isActiveTransform() {
      return this.scale > 1.001 || this.pointers.size >= 2 || this.dragging;
    },
    mediaStyle() {
      return {
        transform: `translate3d(${this.tx}px, ${this.ty}px, 0) scale(${this.scale}) rotate(${this.rotation}deg)`,
        transition: this.pointers.size ? "none" : "transform 0.25s ease",
        transformOrigin: "center center"
      };
    }
  },
  watch: {
    isActiveTransform(v) {
      this.$emit(v ? "lock" : "unlock");
    }
  },
  methods: {
    onLoad() {
      // 图片加载完成
    },
    reset() {
      this.scale = 1;
      this.rotation = 0;
      this.tx = 0;
      this.ty = 0;
      this.pointers.clear();
      this.dragging = false;
      this.$emit("unlock");
    },
    clamp(v, min, max) {
      return Math.max(min, Math.min(max, v));
    },
    // iOS 风格橡皮筋阻尼函数
    rubberBand(distance, dimension, constant = 0.55) {
      return distance * dimension * constant / (dimension + constant * distance);
    },
    getViewportRect() {
      return this.$refs.viewport?.getBoundingClientRect();
    },
    // 计算放大后允许的最大平移范围
    getPanBounds() {
      const rect = this.$refs.viewport?.getBoundingClientRect();
      if (!rect) return {
        maxX: 0,
        maxY: 0,
        vw: 0,
        vh: 0
      };
      const vw = rect.width,
        vh = rect.height;
      const img = this.$el.querySelector('img, video');
      const iw = img?.clientWidth || vw;
      const ih = img?.clientHeight || vh;
      const sw = iw * this.scale;
      const sh = ih * this.scale;
      const maxX = Math.max(0, (sw - vw) / 2);
      const maxY = Math.max(0, (sh - vh) / 2);
      return {
        maxX,
        maxY,
        vw,
        vh
      };
    },
    // 应用边界阻尼
    applyBoundWithRubber(value, max, dimension) {
      if (value > max) {
        return max + this.rubberBand(value - max, dimension, 0.55);
      }
      if (value < -max) {
        return -max - this.rubberBand(-max - value, dimension, 0.55);
      }
      return value;
    },
    calcTwoPointer() {
      const pts = Array.from(this.pointers.values());
      const p0 = pts[0],
        p1 = pts[1];
      const dx = p1.x - p0.x;
      const dy = p1.y - p0.y;
      const dist = Math.hypot(dx, dy);
      const angle = Math.atan2(dy, dx) * (180 / Math.PI);
      const center = {
        x: (p0.x + p1.x) / 2,
        y: (p0.y + p1.y) / 2
      };
      return {
        dist,
        angle,
        center
      };
    },
    // 角度归一化到 -180~180
    normalizeAngle(deg) {
      deg = (deg % 360 + 360) % 360;
      return deg > 180 ? deg - 360 : deg;
    },
    onPointerDown(e) {
      e.currentTarget.setPointerCapture?.(e.pointerId);
      this.viewportRect = this.getViewportRect();
      this.pointers.set(e.pointerId, {
        x: e.clientX,
        y: e.clientY
      });

      // 2指开始：初始化 pinch/rotate 基准
      if (this.pointers.size === 2) {
        const {
          dist,
          angle,
          center
        } = this.calcTwoPointer();
        this.startDist = dist;
        this.startAngle = angle;
        this.startCenter = center;
        this.startScale = this.scale;
        this.startRotation = this.rotation;
        this.startTx = this.tx;
        this.startTy = this.ty;
        this.dragging = false;
        this.gestureMode = null; // 重置手势模式，等 move 时判断
        this.rotateTriggered = false; // 重置旋转触发标记
        return;
      }

      // 1指：如果已放大，则进入拖拽
      if (this.scale > 1.001) {
        this.dragging = true;
        this.dragStart = {
          x: e.clientX,
          y: e.clientY
        };
        this.startTx = this.tx;
        this.startTy = this.ty;
      }
    },
    onPointerMove(e) {
      if (!this.pointers.has(e.pointerId)) return;
      this.pointers.set(e.pointerId, {
        x: e.clientX,
        y: e.clientY
      });

      // 2指：缩放 + 旋转（带死区锁定）
      if (this.pointers.size === 2) {
        e.preventDefault();
        const {
          dist,
          angle,
          center
        } = this.calcTwoPointer();
        const scaleFactor = dist / (this.startDist || dist);
        const scaleChange = Math.abs(scaleFactor - 1);
        const deltaAngle = this.normalizeAngle(angle - this.startAngle);
        const angleChange = Math.abs(deltaAngle);

        // 死区阈值
        const pinchDead = 0.03; // 3% 缩放变化
        const rotateDead = 25; // 25° 才触发旋转（更明显的扭转）

        // 判断手势模式（只在第一次超过死区时锁定）
        if (!this.gestureMode) {
          if (angleChange < rotateDead && scaleChange >= pinchDead) {
            this.gestureMode = 'pinch';
          } else if (scaleChange < pinchDead && angleChange >= rotateDead) {
            this.gestureMode = 'rotate';
          } else if (scaleChange >= pinchDead && angleChange >= rotateDead) {
            // 两个都明显：选更强的那个
            this.gestureMode = angleChange > scaleChange * 200 ? 'rotate' : 'pinch';
          } else {
            // 还在死区内，不动
            return;
          }
        }

        // 根据锁定的模式应用变换
        if (this.gestureMode === 'pinch') {
          this.scale = this.clamp(this.startScale * scaleFactor, this.minScale, this.maxScale);
          // 旋转保持不变
        } else if (this.gestureMode === 'rotate') {
          // 固定 90° 步进旋转，只触发一次
          if (!this.rotateTriggered && angleChange >= rotateDead) {
            this.rotateTriggered = true;
            let newRotation;
            if (deltaAngle > 0) {
              newRotation = this.startRotation + 90; // 顺时针
            } else {
              newRotation = this.startRotation - 90; // 逆时针
            }
            // 归一化到 0, 90, 180, 270 循环
            this.rotation = (newRotation % 360 + 360) % 360;
          }
          // 缩放保持不变
        }

        // 跟随双指中心移动
        if (this.startCenter && this.viewportRect) {
          const cx0 = this.startCenter.x - this.viewportRect.left - this.viewportRect.width / 2;
          const cy0 = this.startCenter.y - this.viewportRect.top - this.viewportRect.height / 2;
          const cx1 = center.x - this.viewportRect.left - this.viewportRect.width / 2;
          const cy1 = center.y - this.viewportRect.top - this.viewportRect.height / 2;
          this.tx = this.startTx + (cx1 - cx0);
          this.ty = this.startTy + (cy1 - cy0);
        }
        return;
      }

      // 1指：拖拽（只在 scale>1 时）+ 边界阻尼
      if (this.dragging && this.scale > 1.001) {
        e.preventDefault();
        const dx = e.clientX - this.dragStart.x;
        const dy = e.clientY - this.dragStart.y;
        const rawX = this.startTx + dx;
        const rawY = this.startTy + dy;
        const {
          maxX,
          maxY,
          vw,
          vh
        } = this.getPanBounds();
        this.tx = this.applyBoundWithRubber(rawX, maxX, vw);
        this.ty = this.applyBoundWithRubber(rawY, maxY, vh);
      }
    },
    onPointerUp(e) {
      if (this.pointers.has(e.pointerId)) this.pointers.delete(e.pointerId);
      if (this.pointers.size < 2) {
        this.startCenter = null;
        this.startDist = 0;
        this.startAngle = 0;
        this.gestureMode = null;
        this.rotateTriggered = false;
      }
      if (this.pointers.size === 0) {
        this.dragging = false;
        // 缩放回到1附近，自动归位
        if (this.scale <= 1.001) {
          this.scale = 1;
          this.tx = 0;
          this.ty = 0;
        } else {
          // 放大状态：回弹到合法范围
          const {
            maxX,
            maxY
          } = this.getPanBounds();
          this.tx = Math.max(-maxX, Math.min(maxX, this.tx));
          this.ty = Math.max(-maxY, Math.min(maxY, this.ty));
        }
      }
    },
    onDblClick() {
      if (this.scale > 1.001) {
        this.scale = 1;
        this.tx = 0;
        this.ty = 0;
      } else {
        this.scale = 2;
      }
    }
  }
};